import {ProductInterface} from '../types/models/product-interface'
import {productsPropertyValues} from '../models/product-property-value'
import {propertyTypes} from '../models/property-types'
import {propertyValues} from '../models/property-values'
import {ProductPropertyValueInterface} from '../types/models/product-property-value-interface'

export interface PropertyInterface {
    id: number;
    propertyTypeId: number;
    value: string;
    costPercent: number;
}

interface PropertyAndTypeInfoInterface {
    name: string;
    data: PropertyInterface[];
}

export interface PropertiesInterface {
    [propertyType: string]: PropertyAndTypeInfoInterface;
}

export function getProperties(product: ProductInterface): PropertiesInterface {
    const propertiesKeysOfPropertyTypeIds: PropertiesInterface = {},
        propertiesKeysOfPropertyTypeType: PropertiesInterface = {},
        // Ищем все значения свойств для продукта
        propertyValuesForProduct: ProductPropertyValueInterface[] = productsPropertyValues.filter(
            item => item.productId === product.id
        )

    // console.log(
    //     'getProperties. Все свойства для этого продукта ',
    //     propertyValuesForProduct
    // )

    propertyValuesForProduct.forEach(item => {
        // console.log('getProperties. Свойство ', item)
        const propertyValue = propertyValues.find(
                propertyValue => item.propertyValueId === propertyValue.id
            ),
            property = {
                ...propertyValue,
                costPercent: item.costPercent,
            }

        if (
            propertiesKeysOfPropertyTypeIds[propertyValue.propertyTypeId] !==
            undefined
        ) {
            propertiesKeysOfPropertyTypeIds[
                propertyValue.propertyTypeId
            ].data.push(property)
        } else {
            propertiesKeysOfPropertyTypeIds[propertyValue.propertyTypeId] = {
                name: null,
                data: [property],
            }
        }
    })

    // console.log(
    //     'propertiesKeysOfPropertyTypeIds ',
    //     propertiesKeysOfPropertyTypeIds
    // )

    for (const propertyTypeId in propertiesKeysOfPropertyTypeIds) {
        const propertyType = propertyTypes.find(
            propertyType => propertyType.id === +propertyTypeId
        )
        propertiesKeysOfPropertyTypeType[propertyType.type] =
            propertiesKeysOfPropertyTypeIds[propertyTypeId]
        propertiesKeysOfPropertyTypeType[propertyType.type].name =
            propertyType.name
    }

    console.log('getProperties ', propertiesKeysOfPropertyTypeType)

    return propertiesKeysOfPropertyTypeType
}
