import * as React from 'react'
import {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import './index.less'
import {RootStateType} from '../../../store/reducers'
import {deleteProductFromBasket} from '../../../store/actions/actionCreators/basket'
import {R} from '../../../navigation/routes'
import {Link} from 'react-router-dom'
import Button from '../../kit/button'
import divideIntegerBySpace from '../../../helpers/divide-integer-by-space'
import {PropertyInterface} from '../../../helpers/get-properties'
import {ProductForBasketInterface} from '../../../types/models/product-for-basket-interface'
import {getCost} from '../../pages/product'

function getPropertiesInfo({
    properties,
    selectedProperties,
}: ProductForBasketInterface): string {
    let propertiesInfo = ''
    for (const propertyType in properties) {
        const property: PropertyInterface = properties[propertyType].data.find(
            property => property.id === selectedProperties[propertyType]
        )
        propertiesInfo +=
            properties[propertyType].name + ': ' + property.value + ', '
    }
    return propertiesInfo.slice(0, propertiesInfo.length - 2)
}

// todo - показывать корзину, только если навели
const BasketWidget: FC = () => {
    const [active, setActive] = useState(false),
        dispatch = useDispatch(),
        basket: ProductForBasketInterface[] = useSelector(
            (state: RootStateType) => state.basket
        )

    let commonSum = 0

    return (
        <div
            className={'basket-widget'}
            onMouseEnter={() => setActive(true)}
            onMouseLeave={() => setActive(false)}
        >
            <span>Корзина</span>
            <ul
                className={`basket-widget__dropdown ${
                    active ? 'basket-widget__dropdown_active' : ''
                }`}
            >
                {basket.length > 0 ? (
                    <>
                        {basket.map((product, i) => {
                            const cost =
                                getCost(
                                    product.cost,
                                    product.selectedProperties,
                                    product.properties
                                ) * product.amount

                            commonSum += cost

                            return (
                                <li
                                    className={'basket-widget__product'}
                                    key={i}
                                >
                                    <div
                                        className={
                                            'basket-widget__product-link-and-cost text_large'
                                        }
                                    >
                                        <div>
                                            <Link
                                                className={
                                                    'basket-widget__product-link'
                                                }
                                                to={`${
                                                    R.CATALOG
                                                }/product-${String(
                                                    product.id
                                                )}`}
                                            >
                                                {product.name}
                                            </Link>
                                            &nbsp;-&nbsp;
                                            {divideIntegerBySpace(cost)} ₽ (
                                            {product.amount}шт.)
                                        </div>
                                        <div
                                            className={
                                                'basket-widget__properties'
                                            }
                                        >
                                            {getPropertiesInfo(product)}
                                        </div>
                                    </div>
                                    <span
                                        className={'basket-widget__delete'}
                                        onClick={() => {
                                            dispatch(deleteProductFromBasket(i))
                                        }}
                                    >
                                        удалить
                                    </span>
                                </li>
                            )
                        })}
                    </>
                ) : (
                    <li>Нет товаров</li>
                )}
                {basket.length > 0 ? (
                    <li
                        className={'basket-widget__footer text_large'}
                        key={'footer'}
                    >
                        Итого:{' '}
                        <span style={{fontWeight: 'bold'}}>
                            {divideIntegerBySpace(commonSum)} ₽
                        </span>
                        <div className={'basket-widget__actions'}>
                            <Button
                                className={'button_primary'}
                                text={'Перейти к оформлению'}
                            />
                        </div>
                    </li>
                ) : null}
            </ul>
            <span className={'basket-widget__sum'}>
                {divideIntegerBySpace(commonSum)} ₽
            </span>
        </div>
    )
}

export default BasketWidget
