import {ProductInterface} from '../../../types/models/product-interface'
import {
    AddProductToBasketActionInterface,
    DeleteProductFromBasketActionInterface,
} from '../../../types/actions'
import T from '../actionTypes'
import {ProductForBasketInterface} from '../../../types/models/product-for-basket-interface'

export function addProductToBasket(
    product: ProductForBasketInterface
): AddProductToBasketActionInterface {
    return {
        type: T.ADD_PRODUCT_TO_BASKET,
        product,
    }
}

export function deleteProductFromBasket(
    productConfigurationId: number
): DeleteProductFromBasketActionInterface {
    return {
        type: T.DELETE_PRODUCT_FROM_BASKET,
        productConfigurationId,
    }
}
