import * as React from 'react'
import {FC, useEffect, useState} from 'react'
import {PropertiesInterface} from '../../../../helpers/get-properties'

export interface SelectedProperties {
    [key: string]: number;
}

interface PropertiesPropsInterface {
    properties: PropertiesInterface;
    onChangePercent?: (percentList: SelectedProperties) => void;
}

const Properties: FC<PropertiesPropsInterface> = ({
    properties,
    onChangePercent,
}) => {
    const selectList = [],
        [selectedValues, setSelectedValues] = useState<SelectedProperties>({})

    useEffect(() => {
        const selectedValues = {}
        for (const propertyType in properties)
            selectedValues[propertyType] = properties[propertyType].data[0].id
        setSelectedValues(selectedValues)
    }, [properties])

    useEffect(() => {
        onChangePercent(selectedValues)
    }, [selectedValues])

    for (const propertyType in properties) {
        const property = properties[propertyType]
        selectList.push(
            <div
                style={{display: 'inline-block', marginRight: '16px'}}
                key={propertyType}
            >
                <div style={{marginBottom: '.2rem'}}>{property.name}</div>
                <select
                    style={{padding: '.2rem .4rem'}}
                    name={property.name}
                    value={selectedValues[propertyType]}
                    onChange={({target: {value}}) => {
                        setSelectedValues(prevState => ({
                            ...prevState,
                            [propertyType]: +value,
                        }))
                    }}
                >
                    {property.data.map(item => {
                        const percent =
                            item.costPercent !== null
                                ? `+${String(item.costPercent)}%`
                                : ''
                        return (
                            <option value={item.id} key={item.id}>
                                {item.value} {percent}
                            </option>
                        )
                    })}
                </select>
            </div>
        )
    }

    return <div>{selectList}</div>
}

export default Properties
