import * as React from 'react'
import {ReactElement, useEffect, useState} from 'react'
import {useDispatch} from 'react-redux'
import {useParams} from 'react-router'
import Row from 'antd/lib/row'
import 'antd/lib/row/style'
import Col from 'antd/lib/col'
import 'antd/lib/col/style'
import Container from '../../layout/container'
import ImgPicker from '../../kit/img-picker'
import Button from '../../kit/button'
import {Cost} from '../../kit/cost'
import {TabInterface, Tabs} from '../../kit/tabs'
import './index.less'
import {ProductInterface} from '../../../types/models/product-interface'
import {products} from '../../../models/products'
import {getCategories} from '../../../helpers/get-categories'
import {CategoryInterface} from '../../../types/models/category-interface'
import ProductCategories from './categories'
import {
    getProperties,
    PropertiesInterface,
    PropertyInterface,
} from '../../../helpers/get-properties'
import Properties, {SelectedProperties} from './properties'
import {addProductToBasket} from '../../../store/actions/actionCreators/basket'
import {ProductForBasketInterface} from '../../../types/models/product-for-basket-interface'

const fakeImages = [
        'http://localhost:8080/dist/src/img/main-banner/5499b595352f2a0022878fa026fd5413.jpg',
        'http://localhost:8080/dist/src/img/main-banner/5499b595352f2a0022878fa026fd5413.jpg',
        'http://localhost:8080/dist/src/img/main-banner/5499b595352f2a0022878fa026fd5413.jpg',
        'http://localhost:8080/dist/src/img/main-banner/5499b595352f2a0022878fa026fd5413.jpg',
    ],
    fakeTabs: TabInterface[] = [
        {
            id: 0,
            name: 'Описание',
            panel: <div>Панель описания</div>,
        },
        {
            id: 1,
            name: 'Отзывы',
            panel: <div>Панель отзывов</div>,
        },
    ]

export function getCost(
    baseProductCost: number,
    selectedProperties: SelectedProperties,
    productProperties: PropertiesInterface
): number {
    console.log('getCost arguments ', selectedProperties, productProperties)

    let productCostWithSize: number = baseProductCost,
        // Часть стоимости, которая высчитывается
        // с учетом наложения процентов других свойств, с учетом размера
        costOfOtherProperties: number = 0

    // Для размера
    if ('size' in selectedProperties) {
        const property: PropertyInterface = productProperties.size.data.find(
            (propertyValue: PropertyInterface) =>
                propertyValue.id === selectedProperties.size
        )
        if (property.costPercent !== null)
            productCostWithSize +=
                (baseProductCost / 100) * property.costPercent
    }

    // Для цвета
    if ('color' in selectedProperties) {
        const property: PropertyInterface = productProperties.color.data.find(
            (propertyValue: PropertyInterface) =>
                propertyValue.id === selectedProperties.color
        )
        if (property.costPercent !== null)
            costOfOtherProperties +=
                (productCostWithSize / 100) * property.costPercent
    }

    return Math.floor(productCostWithSize + costOfOtherProperties)
}

export default function Product(): ReactElement {
    const {id} = useParams(),
        dispatch = useDispatch(),
        [product, setProduct] = useState<ProductInterface>(),
        [categories, setCategories] = useState<CategoryInterface[]>([]),
        [
            productProperties,
            setProductProperties,
        ] = useState<PropertiesInterface>(null),
        [
            selectedProperties,
            setSelectedProperties,
        ] = useState<SelectedProperties>({})

    useEffect(() => {
        const product = products.find(product => product.id === +id)
        setProduct(product)
        setCategories(getCategories(product))
        setProductProperties(getProperties(product))
    }, [id])

    if (product === undefined) return null

    // eslint-disable-next-line one-var
    const costFormatted: number = getCost(
        product.cost,
        selectedProperties,
        productProperties
    )

    return (
        <Container>
            <Row gutter={56} className={'product'}>
                <Col sm={12}>
                    <ImgPicker images={fakeImages} />
                </Col>
                <Col sm={12}>
                    <h1 className={'product__name'}>{product.name}</h1>
                    <p>Артикул: {product.article}</p>
                    <Cost
                        className={'cost_large product__cost'}
                        cost={costFormatted}
                        canceledCost={product.canceledCost}
                    />
                    <Properties
                        properties={productProperties}
                        onChangePercent={setSelectedProperties}
                    />
                    <ProductCategories categories={categories} />
                    <Button
                        className={'button_primary button_large'}
                        text={'Добавить в корзину'}
                        onClick={() => {
                            console.log('Добавить в корзину')
                            const productForBasket: ProductForBasketInterface = {
                                ...product,
                                properties: productProperties,
                                selectedProperties,
                            }
                            dispatch(addProductToBasket(productForBasket))
                        }}
                    />
                </Col>
                <Col sm={20}>
                    <Tabs
                        className={'product__tabs'}
                        tabs={fakeTabs}
                        defaultActiveId={0}
                    />
                </Col>
            </Row>
            <div style={{width: 100, height: 100}}>d</div>
        </Container>
    )
}
